  #all {
    padding: 1%;
    background-color: #ffff01;
    border: 1px solid #707070;
    border-radius: 20px;
    margin-top: 0;
  }
  
  #histWrap {
    height: 82%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: scroll;
  }
  
  table {
    width: 60%;
    height: auto;
    border-collapse: separate;
  }
  
  tr {
    height: 8%;
    width: 20%;
    font-size: large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #ffff01;
  }
  
  th {
      padding: 1%;
    font-weight: normal;
    border: 1px solid #707070;
    border-radius: 15px;
  }
  
  .bold {
    font-size: larger;
    font-weight: bold;
  }
  
